/* eslint-disable array-callback-return */
import { useEffect, useState, useContext } from "react";

// Components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "../../index.css";

// Dependencies
import VideosListAd from "../admin/videosListAdmin";
import { ErrorBoundary } from "./errorsBoundary";
import { ListCategorias } from "../../services/category";
import SearchComponent from "./search";
import Context from "../context/UserContext";
import VideosListUser from "./videoListUser";

/**
 * Función para mostrar los videos en la pantalla principal 
 * @param {object} videos
 * @returns Componente del buscador y componente lista (Dependendiendo si el usuario es admin o no, se retorna un componente distinto)
 */
const VideosList = ({ videos }) => {
  const contenedorCarousel = document.getElementById('carousel');
  const [query, setQuery] = useState("");
  const [categories, setCategories] = useState([]);
  const [searchParam] = useState(["title_espanol", "description_esp"]);
  const [searchParam2] = useState(["categoria"]);
  const { user } = useContext(Context);
  const [filteredVideos, setFilteredVideos] = useState(videos);

  const [filterParam, setFilterParam] = useState("All");

  /**
   * Función para traer la lista de categorias 
   */
 

  useEffect(() => {
    //console.log(videos);
    
    const listCategorias = async () => {
      try {
        const res = await ListCategorias();
        setCategories(res);
      } catch (error) {
        console.log("Error");
      }
    };
    
    listCategorias();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  /**
   * Función para normalizar y remover acentos de un string
   * @param {string} str 
   * @returns {string} String sin acentos
   */
  const removeAccents = (str) => {
    return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
  };

  /**
   * Función para realizar la búsqueda mediante el componente search
   * @param {*} videos 
   * @returns Parámetros de la búsqueda
   */
  const search = (videos) => {
    
    
    if (!query) {
      return videos;
    } else if (query) {
      const queryLowerCase = removeAccents(query.toLowerCase());
      const videosFiltered = videos.filter((dato) => {
        const titleMatches = removeAccents(dato.title_espanol).toString().toLowerCase().indexOf(queryLowerCase) > - 1;
        const descriptionMatches = removeAccents(dato.description_esp).toString().toLowerCase().indexOf(queryLowerCase) > - 1;

        const categoryMatches = dato.categorias.some((categoria) =>
          removeAccents(categoria.categoria.toLowerCase()).includes(queryLowerCase)
        );
        const especialidadMatches = dato.especialidad.some((esp) =>
          removeAccents(esp.especialidad.toLowerCase()).includes(queryLowerCase)
        );
        const subEspecialidadMatches = dato.subEspecialidad.some((esp) =>
          removeAccents(esp.subEspecialidad.toLowerCase()).includes(queryLowerCase)
        );
        const palabraClaveMatches = dato.palabraClave.some((esp) =>
          removeAccents(esp.palabraClave.toLowerCase()).includes(queryLowerCase)
        );
        const autorMatches = dato.autor.some((esp) =>
          removeAccents(esp.autor.toLowerCase()).includes(queryLowerCase)
        );
        return titleMatches || descriptionMatches  || categoryMatches || especialidadMatches || palabraClaveMatches || autorMatches || subEspecialidadMatches; 
      });
      //console.log(videosFiltered);
      return videosFiltered

    } else {      
      return videos.filter((dato) =>
        removeAccents(dato.categorias.categoria.toString())
        .toLowerCase()
        .indexOf(removeAccents(query.toLowerCase())) > -1
      );
    }
  };

  /**
   * Función para realizar el filtro por categoría
   * @param {*} categories 
   * @returns Resultado del filtro
   */
  const search2 = (categories) => {
    return categories.filter((item) => {
      if (contenedorCarousel) {
        if (item.categoria === filterParam) {
          contenedorCarousel.style.visibility = 'hidden';
          contenedorCarousel.style.height = '10px';

          return searchParam2.some((parameter) => {
            return (
              removeAccents(item[parameter])
                .toString()
                .toLowerCase()
                .indexOf(removeAccents(query.toLowerCase())) > -1
            );
          });
        } else if (filterParam === "All") {
          contenedorCarousel.style.visibility = 'visible';
          contenedorCarousel.style.height = '100%';
          return videos.filter((item) => {
            return searchParam.some((parameter) => {
              return (
                removeAccents(item[parameter])
                  .toString()
                  .toLowerCase()
                  .indexOf(removeAccents(query.toLowerCase())) > -1
              );
            });
          });
        }
      } else {
        console.log("tuvimos problemas para cargar el carousel por favor recargar");
      }
    });
  };

  useEffect(() => {
    const results = search(videos);
    
    setFilteredVideos(results);
  }, [query, videos]);

  if (user && user.is_superuser) {
    // Vista de administrador
    return (
      <div>
        <SearchComponent 
          query={query}
          setQuery={setQuery}
          filterParam={filterParam}
          setFilterParam={setFilterParam}
          categories={categories}
        />
        {filteredVideos.length > 0 ? (
          <VideosListAd
            videos={filteredVideos}
            categories={categories}
            search={search}
            search2={search2}
          />
        ) : (
          <h1 className="m-5 text-center">No se encontraron videos</h1>
        )}
      </div>
    );
  }
  
  // Vista de usuario o visitante (sin iniciar sesión)
  return (
    <div>
      <SearchComponent 
        query={query}
        setQuery={setQuery}
        filterParam={filterParam}
        setFilterParam={setFilterParam}
        categories={categories}
      />
      <ErrorBoundary>
        {filteredVideos.length > 0 ? (
          <VideosListUser
            videos={filteredVideos}
            categories={categories}
            search={search}
            search2={search2}
          />
        ) : (
          <h1 className="m-5 text-center">No se encontraron videos</h1>
        )}
      </ErrorBoundary>
    </div>
  );
  
 
};

export default VideosList;
