import { useEffect, useState, useContext } from "react";

//dependencies
import VideosList from "../components/videos/videosList";
import VideoLoadingComponent from "../components/videos/videoLoading";
import * as VideoServer from "../services/videoServer";
//components
import { Container } from "@material-ui/core";
import PropTypes from "prop-types";
import Context from "../components/context/UserContext";

/**
 * Carga todos los videos para el Home de la aplicación
 */
const Principal = () => {
  const { user } = useContext(Context);
  const VideoLoading = VideoLoadingComponent(VideosList);

  const [appState, setAppState] = useState({
    loading: true,
    videos: [],
  });

  useEffect(() => {
    const fetchVideos = async () => {
      try {
        const res = user
          ? await VideoServer.ListVideos()
          : await VideoServer.ListVideosPublic();
  
        const allVideos = res
        //console.log(allVideos);
        
        setAppState({ loading: false, videos: allVideos.videos });
      } catch (error) {
        console.error("Error cargando videos", error);
        setAppState({ loading: false, videos: [] });
      }
    };
  
    fetchVideos();
  }, [user]);
  

  return (
    <div className="App">
      <VideoLoading isLoading={appState.loading} videos={appState.videos} />
    </div>
  );
};

Principal.propTypes = {
  loading: PropTypes.bool,
  videos: PropTypes.object,
};

export default Principal;
